.cntr {
  &-bl {
    position: relative;

    &.active {
      .cntr-ls {
        display: block; }

      .cntr-arrow {
        transform: rotateX(180deg);
        top: calc(50% - 5px*2); }

      .cntr-sl:after {
        transform: rotateX(180deg);
        top: calc(50% - 2px*2); } }

    .cntr-in-sl {
      padding-left: 50px; } }

  &-ls {
    width: 100%;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #304163;
    padding: 20px 20px 20px 35px;
    max-height: 100vh;
    z-index: 2;
    display: none;

    ul {
      max-height: 280px;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 2px;
        background: #2b3a5a; }


      &::-webkit-scrollbar-thumb {
        background-color: #2bc39c; } }

    li {
      font-size: 14px;
      color: #9fabc3;
      letter-spacing: .65px;
      cursor: pointer;
      margin-bottom: 5px;
      transition: all .3s;
      display: flex;
      align-items: center;

      span {
        margin-right: 10px; }

      @media(max-width: 768px) {
        margin-bottom: 15px; }

      &:hover {
        color: #46ebc1; } } }

  &-sl {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 50px;
    cursor: pointer;
    display: flex;
    align-items: center;

    &:after {
      content: '';
      position: absolute;
      border: 3px solid transparent;
      border-top: 3px solid #465e7c;
      top: calc(50% - 3px/2);
      left: 35px;
      width: 0;
      height: 0;
      transition: all .4s; }

    span {
      left: 10px; } }

  &-in {
    cursor: pointer; }

  &-middle {
    ul {
      max-height: 220px; } }



  &-top {
    top: 0;
    transform: translateY(-100%); }

  &-arrow {
    display: block;
    position: absolute;
    border: 5px solid transparent;
    border-top: 5px solid #465e7c;
    top: calc(50% - 5px/2);
    right: 10px;
    width: 0;
    height: 0;
    transition: all .4s;
    cursor: pointer; } }

